import React,{Component} from 'react';
import logo from './Planet9_3840x2160.jpg';
import classes from './App.module.css';
import ProductDetail from './ProductDetails/ProductDetail.js';
import ProductPreview from './ProductPreview/ProductPreview';
import ProductData from './ProductData';


class App extends Component{
  state={
    ProductData:ProductData,
    ColorImagePos:0,
    FeatureItemPos:0
  }
  OnColorOptionClick=(pos)=>{
    this.setState({ColorImagePos:pos});
  };
  OnFeatureClick=(pos)=>{
    this.setState({FeatureItemPos:pos});
  }
  render(){
    return (
    <div className="App">
      <header className="App-header">
        <nav className={classes.Topbar}>
          <img src={logo} alt="Logo"/>
        </nav>

        <div className={classes.MainContainer}>
            <div className={classes.ProductPreview}>
              <ProductPreview ColorImage={this.state.ProductData.colorOptions[this.state.ColorImagePos].imageUrl} FeatureItemPos={this.state.FeatureItemPos}/>
            </div>
            <div className={classes.ProductData}>
              <ProductDetail FeatureItemPos={this.state.FeatureItemPos} data={this.state.ProductData} OnColorOptionClick={this.OnColorOptionClick} ImagePos={this.state.ColorImagePos} OnFeatureClick={this.OnFeatureClick}/>

            </div>
        </div>
      </header>
    </div>
  );
  }
}


export default App;
