import React from 'react';
import classes from './ProductDetail.module.css';

const ProductDetail=(props)=>{
    const ColorOption=props.data.colorOptions.map((item,pos)=>{
        const classArr=[classes.ProductImage];
        if(pos===props.ImagePos){
            classArr.push(classes.SelectedProductImage);
        }
        return (<img key={pos} className={classArr.join(" ")} src={item.imageUrl} alt={item.styleName}
        onClick={()=>props.OnColorOptionClick(pos)}/>
        );
    });


    const features=props.data.featureList.map((item,pos)=>{
        const classArr=[classes.FeaturesItem];
        if(pos===props.FeatureItemPos){
            classArr.push(classes.SelectedFeaturesItem);
        }
        return (
        <button key={pos} className={classArr.join(" ") }onClick={()=>props.OnFeatureClick(pos)}>{item}</button>
        );
    })
    return (
        <div className={classes.ProductData}>
              <h1>{props.data.title}</h1>
              <p>{props.data.description}</p>
              <div className={classes.ImageCollection}>
                <h1>Select Color</h1>
                {ColorOption}
              </div>
              <h1>Feature</h1>
              <div className={classes.FeaturesContainer}>
                {features}
              </div>
              <button className={classes.Submit}>Submit</button>

            </div>
    );
}

export default ProductDetail;